import React, {useState} from "react";
import {NavLink} from "react-router-dom";
import _ from "lodash";

const Header = () => {
    const [Link] = useState({
        links: [
            {
                link: "/",
                name: "Services",
                icon: <i className="fas fa-user-cog"/>,
            },
            {
                link: "/works",
                name: "Works",
                icon: <i className="fas fa-folder-open"/>,
            },
            {
                link: "/notes",
                name: "Notes",
                icon: <i className="fas fa-sticky-note"/>,
            },
            {
                link: "/contacts",
                name: "Contacts",
                icon: <i className="fas fa-address-book"/>,
            },
        ],
    });
    return (
        <span className="header__links small-text">
        {_.map(Link.links, (link) => {
            return (
                <NavLink
                    key={link.link}
                    exact
                    to={link.link}
                    activeClassName="active">
                    <span className="arrows">{"<"}</span>
                    {link.icon}
                    {link.name}
                    <span className="arrows">{">"}</span>
                </NavLink>
            );
        })}
      </span>
    );
}

export default Header;
