import React from "react";
import classNames from "classnames";
import AppUtil from "../utils/AppUtil";

const Screen = (props) => {
    return (
        <div
            id={AppUtil.getScreenIdByN(props.screens, props.nth)}
            className={classNames("screen", props.className)}
        >
            {props.children}
        </div>
    );
};


export default Screen;
