import React, {useEffect, useState} from 'react';
import API from "../../src/API"
import Screen from "../components/Screen";
import Tooltip from "rc-tooltip";


const Works = () => {
    const state = useState({
        screens: [{id: "screen-profile"}, {id: "screen-work"}],
    });
    const [works, setWorks] = useState([]);

    useEffect(() => {
        API.get("").then((repos) => {
            setWorks(repos.data);
        });
    }, [setWorks]);

    return (
        <div className="page" id="home">
            <Screen screens={state.screens} nth={1}>
                <div className="profile">
                    <div className="page">
                        <div className="work">
                            {
                                works.map((data) => {
                                    return (
                                        <div key={data.id} className="work_cards">
                                            <div className="work_cards_head">
                                                <div className="work_cards_items_head">{data.name}</div>
                                                <div className="work_cards_items">{data.language}</div>
                                            </div>
                                            <div className="work_cards_icon">
                                                <i className="fab fa-js fa-5x"/>
                                            </div>
                                        </div>
                                    );
                                })
                            }
                            {
                                works.map((data) => {
                                    return (
                                        <div key={data.id} className="work_cards">
                                            <div className="work_cards_head">
                                                <div className="work_cards_items_head">{data.name}</div>
                                                <div className="work_cards_items">{data.language}</div>
                                            </div>
                                            <div className="work_cards_icon">
                                                <i className="fab fa-js fa-5x"/>
                                            </div>
                                        </div>
                                    );
                                })
                            }                                                                                                                                                                                                                                                                                                                                                                                   
                        </div>
                    </div>
                </div>
            </Screen>
        </div>

    );
};

export default Works;
